// axios 設定
import axios from 'axios'
import qs from "qs";

import store from '@/store';  //直接引用vuex中的store內容

//axios基本設定 -> 及使用 instance取代axios 實例
const instance = axios.create({
  baseURL: store.state.base_url, //統一使用 vuex 的 store 裡的base_url
  // headers: { 'Content-Type': 'application/json' },
  timeout: 120000
});


// Do something before request is sent 發送資料到伺服器前準備
instance.interceptors.request.use(function (config) {
  console.log('beforesend',1)
  store.commit("setLoadingOn"); //讀取畫面on

  return config;
}, function (error) {
  console.log('beforesend', 2)
  // Do something with request error
  return Promise.reject(error);
});

// Add a response interceptor 伺服器回覆狀況控制
instance.interceptors.response.use(function (response) {
  // Any status code that lie within the range of 2xx cause this function to trigger
  // Do something with response data
  //伺服器正常回覆時
  // console.log('(ok state):', response)

  //故意顯示0.5秒的動畫
  setTimeout(() => {
    store.commit("setLoadingOff"); //讀取畫面off
  }, 500)
  return response;
}, function (error) {
  // Any status codes that falls outside the range of 2xx cause this function to trigger
  // Do something with response error
  //伺服器回覆異常時
  // console.log('(error state):', error)
  store.commit("setLoadingOff"); //讀取畫面off
  alert(error)

  //本地網路不通時,回傳 "network error"
  if (!navigator.onLine) {
    console.log("網路出了點問題，請重新連線後重整網頁");
    alert("網路出了點問題，請確認連線後重整網頁");
    return Promise.reject("network error");
  }

  //判斷伺服器回應狀況
  if (error.response) {
    switch (error.response.status) {
      case 404:
        console.log("你要找的頁面不存在")
        // let pop = { msg: "資料不存在", type: true };
        // store.commit("snackbar", pop);
        // go to 404 page
        break
      case 500:
        console.log("程式發生問題")
        // go to 500 page
        break
      default:
        console.log(error.message)
    }
  }else{
    //伺服器無回應
    alert("伺服器沒有回應");
    return Promise.reject("server not working");
  }
  
  return Promise.reject(error);
});


var api = {
  //get data
  get(database) {
    // axios.post("general/edit/" + store.state.databaseName + "/" + database, qs.stringify(data.postData))
    return new Promise((resolve, reject) => {
      instance.post(`general/getAll/${store.state.databaseName}/${database}`)
        .then((rs) => {
          resolve(rs.data);
        })
        .catch((err) => {
          reject(err);
        });
    });
  },

  //add data
  add(database, data) {
    return new Promise((resolve, reject) => {
      instance.post(`general/add/${store.state.databaseName}/${database}`, qs.stringify(data))
        .then((rs) => {
          resolve(rs.data);
        })
        .catch((err) => {
          reject(err);
        });
    });
  },



  //post data (edit , update)
  post(database, data) {
    return new Promise((resolve, reject) => {
      instance.post(`general/edit/${store.state.databaseName}/${database}`, qs.stringify(data))
        .then((rs) => {
          resolve(rs.data);
        })
        .catch((err) => {
          reject(err);
        });
    });
  },

  //delete data
  delete(database, data = null) {
    return new Promise((resolve, reject) => {
      instance.post(`general/delv3/${store.state.databaseName}/${database}`, qs.stringify(data))
        .then((rs) => {
          resolve(rs.data);
        })
        .catch((err) => {
          reject(err);
        });
    });
  },

  //特別處理 url自定義
  options(url, data = null) {
    return new Promise((resolve, reject) => {
      instance.post(`${url}`, qs.stringify(data))
        .then((rs) => {
          resolve(rs.data);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }
}

export default api;