import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    colorSet: {
      bgColor1: "#D1DDDB",
      bgColor2: "#85B8CB",
      bgColor3: "#1D6A96",
      bgColor4: "#283B42"
    },
    verMsg:"v.2023.9.13.1", //版本訊息
    snackbar: false,  //是否顯示訊息列
    snackbarMsg: '',  //訊息列內容
    snackbarTheme: '',  //訊息列樣式

    logined: false,  //登入情況->初設值為false
    time: '',  //現在時間
    loading: "",  //讀取介面顯示用,介面放在main.vue裡

    // base_url:'http://127.0.0.1/longcaresys_vueapi/', //單機專用
    base_url: 'https://www.pddtvgame.com/longcaresys_vueapi/',  //線上專用
    // base_url: 'http://192.168.2.209/longcaresys_vueapi/',  //linux 測試伺服器

    databaseName: 'lc_yuehao',  //登入廠商代號及讀取的資料庫名(悅豪)
    // databaseName: 'lc_yueron',  //登入廠商代號及讀取的資料庫名(悅榕)
    // mohw_url: 'http://114.34.167.172:49153/NursingHome/API/nhcApi', //衛服部的base_url (測試機)
    mohw_url: 'https://nhc.mohw.gov.tw/nhcapi/API/nhcApi/', //衛服部的base_url (正式機)
               
    //暫存用資料
    cData: {},  //廠商資料
    pData: {},  //使用者
    uData: {},  //病歷

    userItems:[], //全部病歷資料
    bed_usedItems: [], //使用病床資料
    behospitalizedItems: [], //請假資訊

    personnelItems:[], //全部護理人員資料
    usingItems: [], //處理中頁面暫存資料
    medicationItems:[], //暫存藥師資料

    analysisofevaluationindicatorsItems:null, //已存檔的六大評估資料

    //功能名稱,樣式,key值,引用圖片,授權,icon圖片設定,
    authKeys: [
      {
        label: "病歷管理&維護", class: "ma-1", color: "primary", keyName: 'user',
        image: require("@/assets/img/computer_hakui_doctor_woman.png"),
        route: "/main/userlist",
        authKey: "user_key",
        icon: 'mdi-badge-account-horizontal',
      },
      {
        label: "生命徵象紀錄單(多單)", class: "ma-1", color: "primary", keyName: 'pass',
        image: require("@/assets/img/kenkoushindan_ketsuatsu.png"),
        route: "/main/signlifelistmulti",
        authKey: "pass",
        icon: 'mdi-table-large',
      },
      {
        label: "交班表", class: "ma-1", color: "success", keyName: 'pass',
        image: require("@/assets/img/hanashiai_nurse_woman.png"),
        route: "/main/handoverlist",
        authKey: "pass",
        icon: 'mdi-text-box-outline',
      },
      {
        label: "活動參與紀錄表", class: "ma-1", color: "warning", keyName: 'pass',
        image: require("@/assets/img/party_christmas.png"),
        route: "/main/activitylist",
        authKey: "pass",
        icon: 'mdi-ticket',
      },
      {
        label: "床位管理", class: "ma-1 white--text", color: "pink", keyName: 'bed',
        image: require("@/assets/img/kagu_sleep_futon_shiku.png"),
        route: "/main/bedlist",
        authKey: "bed_key",
        icon: 'mdi-bed-empty',
      },
      {
        label: "會計管理", class: "ma-1 white--text", color: "teal", keyName: 'accounting',
        image: require("@/assets/img/money_choubo.png"),
        route: "/main/accountinglist",
        authKey: "accounting_key",
        icon: 'mdi-cash',
      },
      {
        label: "人員管理", class: "ma-1 white--text", color: "purple", keyName: 'personnel',
        image: require("@/assets/img/taiin_miokuri.png"),
        route: "/main/personnellist",
        authKey: "personnel_key",
        icon: 'mdi-account',
      },
      {
        label: "住民分析", class: "ma-1 white--text", color: "purple", keyName: 'pass',
        image: require("@/assets/img/document_data_bunseki.png"),
        route: "/main/statisticalanalysis",
        authKey: "pass",
        icon: 'mdi-google-analytics',
      },
      {
        label: "照會討論紀錄表", class: "ma-1 white--text", color: "purple", keyName: 'pass',
        image: require("@/assets/img/kangoshi_kaigi.png"),
        route: "/main/notediscussionlist",
        authKey: "pass",
        icon: 'mdi-disqus',
      },
      {
        label: "疫情訪視預約單", class: "ma-1 white--text", color: "purple", keyName: 'pass',
        image: require("@/assets/img/template_calender_j.jpg"),
        route: "/main/vieworderlist",
        authKey: "pass",
        icon: 'mdi-book-open-page-variant',
      },
      {
        label: "公告欄", class: "ma-1 white--text", color: "indigo light-2", keyName: 'mainboard',
        image: require("@/assets/img/keijiban.png"),
        route: "/main/mainboardlist",
        authKey: "mainboard_key",
        icon: 'mdi-clipboard',
      },
      {
        label: "指標監測分析", class: "ma-1 white--text", color: "indigo light-2", keyName: 'qualityindex',
        image: require("@/assets/img/whiteboard_memo_woman2.png"),
        route: "/main/AnalysisOfEvaluationIndicators",
        authKey: "qualityindex_key",
        icon: 'mdi-chart-arc',
      },
      
      {
        label: "登出", class: "ma-1 white--text", color: "dark", keyName: 'exit',
        image: require("@/assets/img/text_exit.png"),
        route: "/login",
        authKey: "exit_key",
        icon: 'mdi-logout',
      },
    ],
  },

  mutations: {
    getTime(state) {  //取得現在日期,時間
      var time = new Date();
      state.time = (
        time.getFullYear() +
        "-" +
        (time.getMonth() +
          1) +
        "-" +
        time.getDate() +
        " " +
        time.getHours() +
        ":" +
        time.getMinutes() +
        ":" +
        time.getSeconds()
      );
    },
    snackbar(state, val) {  //狀態列內容
      state.snackbarMsg = val.msg;
      state.snackbar = val.type;
      state.snackbarTheme = val.theme;
    },
    logined(state, val) {  //切換登入
      state.logined = val
    },
    setcData(state, val) {  //設定廠商資料
      state.cData = val
    },
    setpData(state, val) {  //設定登入者資料
      state.pData = val
    },
    setuData(state, val) {  //設定選取的病歷資料
      state.uData = val
    },
    setLoadingOn(state) {  //設定讀取畫面On
      state.loading = true
    },
    setLoadingOff(state) {  //設定讀取畫面Off
      state.loading = false
    },
    setData(state, val) {
      state[val.tableName + 'Items'] = val.data;
    },
  },

  getters: {
    
  },

  actions: {
    getUsingItems(context, tableName) { //取得某頁面中主要使用的全部資料
      context.commit("setLoadingOn"); //讀取畫面on
      axios.post("general/getAll/" + this.state.databaseName + "/" + tableName)
        .then((rs) => {
          context.commit("setLoadingOff"); //讀取畫面off
          context.commit('setData',
            {
              tableName: 'using',
              data: rs.data
            });
        });
    },
    getItems(context, tableName) { //取得XX全部資料
      axios.post(
        "general/getAll/" + this.state.databaseName + "/" + tableName
      ).then((rs) => {
        context.commit('setData',
          {
            tableName: tableName,
            data: rs.data
          });
      });
    },
    //上傳檔案用
    upLoad(context, data) {
      return new Promise((resolve, reject) => {
        axios.post(data.url, data.fd, {
          onUploadProgress: uploadEvent => {
            console.log('upload progress' + Math.round(uploadEvent.loaded / uploadEvent.total * 100) + '%');
          }
        })
          .then((rs) => {
            resolve(rs.data);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },

    //評估指標分析用Start
    async getUser(context) {
      // console.log(1)
      //取得所有資料
      await axios.post("user/getAll/" + this.state.databaseName).then(
        (rs) => {
          context.commit('setData',
            {
              tableName: 'user',
              data: rs.data
            });
        }
      );
    },
    async getBedUsed(context) {
      // console.log(2)
      //取得所有資料,不含H床
      await axios.post(
        "bed_used/getAll/" + this.state.databaseName + "/bed_used"
      ).then((rs) => {
        const matchData = rs.data.filter((i) => {
          return !i.bname.includes("H");
        });
        let finalMData = matchData.map((i) => {
          let newItem = i;
          const matchUser = this.state.userItems.find((user) => {
            return user.snkey == i.u_snkey;
          });
          if (matchUser) {
            newItem.userData = matchUser;
          } else {
            newItem.userData = "";
          }
          return newItem;
        });

        context.commit('setData',
          {
            tableName: 'bed_used',
            data: finalMData
          });
      });
    },
    async getBehospitalized(context) {
      // console.log(3)
      await axios.post(
        "bed_used/getAll/" + this.state.databaseName + "/behospitalized"
      ).then((rs) => {
        let matchData = rs.data.map((i) => {
          var newItem = i;
          const matchItem = this.state.userItems.find((user) => {
            return user.snkey == i.u_snkey;
          });
          if (matchItem) {
            newItem.userData = matchItem;
          }
          return newItem;
        });

        context.commit('setData',
          {
            tableName: 'behospitalized',
            data: matchData
          });
      });
    },
    //評估指標分析用end
  },
  modules: {
  }
})
