import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify';
import "@/assets/css/mytransition.css"  //自己設定的各項過場動畫
import "@/assets/css/mystyle.css"  //自己設定css

/*
2022.3.6 導入全局使用的axios 主要的get, post, delete 都寫在api.js裡
之後各畫面用時使用this.$api再去call 相對應的動作就可以了
*/
import api from "@/assets/js/api.js";
Vue.prototype.$api = api

//bootstrap 引用
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
Vue.use(BootstrapVue)
Vue.use(IconsPlugin)

// axios 設定
import axios from 'axios'
import VueAxios from 'vue-axios'
import qs from "qs";

Vue.use(VueAxios,axios)
axios.defaults.baseURL = store.state.base_url  //直接指定store裡的base_url(伺服器時用)
