//vue confirm的插件及設定
import VuetifyConfirm from 'vuetify-confirm'
Vue.use(VuetifyConfirm, { 
  vuetify,
  buttonTrueText: '確認',
  buttonFalseText: '取消',
  color: 'error',
  icon: 'mdi-alert',
  title: '警告',
})

Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
