<template>
    <v-app id="app" color="grey lighten-4">
      <!-- 訊息顯示 -->
      <transition name="bounce">
        <v-snackbar
          top
          :color="this.$store.state.snackbarTheme"
          :value="this.$store.state.snackbar"
          @input="close"
        >
          <span>{{ this.$store.state.snackbarMsg }}</span>
          <v-btn text @click.native="close(false)">Close</v-btn>
        </v-snackbar>
      </transition>

      <Loading v-if="$store.state.loading" /> <!-- 讀取畫面 自已設計的loading -->

      <v-content>
        <transition name="fade" mode="out-in" appear>
          <router-view></router-view>
        </transition>
      </v-content>
      
    </v-app>
</template>

<script>
import Loading from '@/components/loading.vue' //自己設計的讀取畫面;透過v-if 控制開關

export default {
  components:{ Loading },
  name: 'App',
  
  data: () => ({
    //
  }),

  methods:{
    close(val) {
      var pop = { msg: "", type: val };
      this.$store.commit("snackbar", pop);
    },
  }
};
</script>

<style lang="scss">
#app {
  font-family: '微軟正黑體';
}
.v-snack__content {
  font-size: 2rem;
}
</style>